/* Profile.css  Enhanced styling for UserProfile */

/* Base & Container */
.profile-page {
  max-width: 1200px;
  margin: 0 auto;
  padding: 1.5rem;
  min-height: 100vh;
  background: var(--bg-light);
}

/* Loading & Error States */
.profile-page .loading,
.profile-error {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 60vh;
  text-align: center;
  background: var(--white);
  border-radius: var(--radius-lg);
  padding: 3rem;
  box-shadow: var(--shadow-sm);
}

.profile-error h2 {
  color: var(--error);
  margin-bottom: 1rem;
}

.profile-error p {
  color: var(--text-secondary);
  margin-bottom: 2rem;
}

/* Header Section */
.profile-header {
  background: var(--white);
  border-radius: var(--radius-lg);
  padding: 2rem;
  margin-bottom: 1.5rem;
  box-shadow: var(--shadow-sm);
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  position: relative;
}

.back-btn {
  background: var(--bg-lighter);
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: var(--radius-sm);
  color: var(--text-primary);
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 1rem;
  width: fit-content;
}

.back-btn:hover {
  background: var(--border-color);
  transform: translateX(-4px);
}

.header-left {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  flex: 1 1 auto;
}

.profile-avatar-large {
  flex-shrink: 0;
}

.profile-info h1 {
  font-size: 2rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 0.25rem;
}

.profile-info p {
  color: var(--text-secondary);
  margin: 0.25rem 0;
}

.member-since {
  font-size: 0.875rem;
  color: var(--text-tertiary);
}

.header-actions {
  display: flex;
  gap: 1rem;
  margin-left: auto;
}

.btn {
  padding: 0.75rem 1.5rem;
  border-radius: var(--radius-sm);
  font-weight: 600;
  font-size: 1rem;
  border: none;
  cursor: pointer;
  transition: all 0.2s;
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
}

.btn-primary {
  background: var(--mubas-blue);
  color: white;
  box-shadow: 0 4px 6px rgba(0, 58, 143, 0.2);
}

.btn-primary:hover {
  background: var(--mubas-blue-dark);
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.btn-secondary {
  background: var(--bg-lighter);
  color: var(--text-primary);
}

.btn-secondary:hover {
  background: var(--border-color);
  transform: translateY(-2px);
}

/* Tabs Navigation */
.profile-tabs {
  background: var(--white);
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-sm);
  overflow: hidden;
}

.tabs-navigation {
  display: flex;
  gap: 0.5rem;
  padding: 1rem 1.5rem 0;
  border-bottom: 2px solid var(--border-light);
}

.tab-btn {
  padding: 0.75rem 1.5rem;
  background: transparent;
  border: none;
  font-weight: 600;
  color: var(--text-tertiary);
  cursor: pointer;
  position: relative;
  transition: color 0.2s;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.tab-btn i {
  font-size: 1.1rem;
}

.tab-btn.active {
  color: var(--mubas-blue);
}

.tab-btn.active::after {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 0;
  right: 0;
  height: 3px;
  background: var(--mubas-blue);
  border-radius: var(--radius-xs) var(--radius-xs) 0 0;
}

.tabs-content {
  padding: 2rem 1.5rem;
}

/* Profile Tab Content */
.profile-tab-content {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

/* Bio Section */
.profile-bio-section h3,
.profile-details-section h3,
.profile-interests-section h3,
.profile-photos-section h3 {
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.bio-text {
  background: var(--bg-lighter);
  padding: 1.5rem;
  border-radius: var(--radius-md);
  color: var(--text-secondary);
  line-height: 1.6;
  font-size: 1rem;
}

/* Details Grid */
.details-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
  gap: 1rem;
}

.detail-item {
  background: var(--bg-lighter);
  padding: 1rem 1.25rem;
  border-radius: var(--radius-sm);
  display: flex;
  flex-direction: column;
  transition: transform 0.2s;
}

.detail-item:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-sm);
}

.detail-label {
  font-size: 0.75rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  color: var(--text-tertiary);
  margin-bottom: 0.25rem;
}

.detail-value {
  font-weight: 600;
  color: var(--text-primary);
  font-size: 1rem;
}

/* Interests Grid */
.interests-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 0.75rem;
}

.interest-item {
  background: linear-gradient(135deg, var(--mubas-blue), var(--mubas-blue-dark));
  color: white;
  padding: 0.5rem 1.25rem;
  border-radius: 30px;
  font-size: 0.9rem;
  font-weight: 500;
  box-shadow: 0 2px 8px rgba(0, 58, 143, 0.2);
  transition: all 0.2s;
}

.interest-item:hover {
  transform: scale(1.05);
  box-shadow: 0 4px 12px rgba(0, 58, 143, 0.3);
}

/* Photos Grid */
.photos-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
  margin-top: 1rem;
}

.photo-item {
  position: relative;
  aspect-ratio: 1 / 1;
  border-radius: var(--radius-md);
  overflow: hidden;
  box-shadow: var(--shadow-sm);
  transition: transform 0.3s;
}

.photo-item:hover {
  transform: scale(1.02);
  box-shadow: var(--shadow-md);
}

.photo-preview {
  width: 100%;
  height: 100%;
  background-color: var(--bg-lighter);
  position: relative;
}

.primary-badge {
  position: absolute;
  top: 8px;
  left: 8px;
  background: var(--gold-gradient);
  color: var(--text-primary);
  padding: 0.25rem 0.75rem;
  border-radius: 30px;
  font-size: 0.75rem;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 0.25rem;
  box-shadow: var(--shadow-sm);
  z-index: 1;
}

/* Responsive Design */
@media (max-width: 768px) {
  .profile-page {
    padding: 1rem;
  }

  .profile-header {
    flex-direction: column;
    align-items: stretch;
    padding: 1.5rem;
  }

  .back-btn {
    margin-bottom: 1rem;
    align-self: flex-start;
  }

  .header-left {
    flex-direction: column;
    text-align: center;
    gap: 1rem;
    margin-bottom: 1.5rem;
  }

  .profile-info h1 {
    font-size: 1.75rem;
  }

  .header-actions {
    justify-content: center;
    margin-left: 0;
    width: 100%;
  }

  .btn {
    flex: 1;
    justify-content: center;
  }

  .tabs-navigation {
    justify-content: center;
  }

  .tab-btn {
    flex: 1;
    justify-content: center;
  }

  .details-grid {
    grid-template-columns: 1fr;
  }

  .photos-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 480px) {
  .photos-grid {
    grid-template-columns: 1fr;
  }

  .interest-item {
    font-size: 0.8rem;
    padding: 0.4rem 1rem;
  }
}

/* Additional Polish */
hr {
  border: none;
  border-top: 1px solid var(--border-color);
  margin: 1.5rem 0;
}

i {
  font-style: normal; /* assuming font awesome icons */
}

/* Gradient fallback for photos without image */
.photo-preview[style*="linear-gradient"] {
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 2rem;
}